/* ===  HEADER === */

header.sitepoint {
  background: #333;
  color: #ddd;
  position: fixed;
  width: 100%;
  .branding {
    height: $line-height * 2;
    margin: 0;
    .branding-logo {
      height: $line-height;
      margin-top: 1rem;
    }
  }
  .col-3 {
    padding: 0 0rem;
  }
  .dash-controls {
    padding-top: 0.888889rem;
  }
}

/* ===  NAVBAR === */

nav.main {
  background: #fff;
  box-shadow: 0 .3rem .35rem rgba(0, 0, 0, .075);
  .col {
    padding: 0 .5rem 0;
  }
}

.navbar {
  margin: 0;
  padding: 0;
  font-weight: 500;
  line-height: 1.7777778rem;
  li {
    display: inline-block;
    font-size: .85rem;
    padding: 0 1rem;
    color: #666
  }
}

/* ===  BOOKTITLE === */

section.titling {
  padding-top: 1.7777778rem * 2.5;
  letter-spacing: .05rem
}

h1.book-title {
  color: #003
}

p.book-breadcrumbs {
  margin: 1.7777778rem 0 -1.7777778rem;
  font-size: .75rem;
  color: $textgray;
}

p.book-author {
  margin: -0.8888889rem*3 0 0.0rem;
  font-size: .75rem;
  color: $textgray;
}

/* ===  HERO === */

section.hero {
  height: 7*1.7777778rem;
  background-color: rgb(38, 38, 38);
  background-image: url("https://dab1nmslvvntp.cloudfront.net/wp-content/uploads/2016/11/14788516501466142151pattern-final-copy.png"), linear-gradient(165deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0) 65%, rgba(38, 38, 38, 0.3) 65.1%, rgba(38, 38, 38, 0.3));
}

.heroPitch {
  color: #fff;
  line-height: 2.6666667rem
}

/* ===  PRODUCT & SPECS === */

.book {
  top: -2rem; // positioning the book model up higher in the page.
  margin-bottom: -3rem;
  left: 5rem;
}

.course {
  top: -2rem; // positioning the course preview up higher in the page.
  margin-bottom: -3rem;
  left: 5rem;
  .cover {
    border: 10px solid #333;
    position: relative;
    &:before {
      display: block;
      position: absolute;
      content: '';
      width: 5rem;
      height: 5rem;
      left: 50%;
      top: 50%;
      z-index: 9999;
      margin: -2.5rem -2.5rem;
      background-image: url(/~awalker/images/loop2.svg);
      //outline: 1px red dashed;
      background-size: 100% auto;
    }
    img {
      width: 100%;
    }
  }
}

.test {
  -webkit-animation: test 3s;
  animation: test 3s;
}

ul.specs {
  padding: 0;
  list-style-type: none;
  margin: 4*1.7777778rem 10% 0;
  box-shadow: .1rem .1rem 1rem rgba(38, 38, 38, 0.2);
  li.icon {
    border: 1px solid #CFCFCF;
    background: #fff;
    position: relative;
    line-height: 2.6666667rem;
    margin-bottom: -1px;
    padding-left: 2rem;
    z-index: -1;
    i,
    svg {
      margin-right: .88888889rem;
    }
  }
}

/* ===  WHO? === */

section.whoisthisfor {
  background: #f3f3f3; //padding: 1.7777778rem 0;
  h2 {
    text-align: left;
  }
  div {
    text-align: center;
    img {
      width: 46.5%;
      margin: 0 0 -1.7777778rem;
    }
  }
}

/* ===  WHAT YOU'LL LEARN === */

.whatyoulllearn {}

.blurb h4 {
  font-weight: 600;
}

.blurb h5 {
  margin: 0 0 0 0;
  color: #999
}

.blurb p {
  font-size: 0.85rem;
}

/* ===  AUTHOR/TEACHER === */

.author-avatar {
  text-align: center;
  img {
    border-radius: 50%;
    width: 50%;
  }
}

.authors {
  .fa-2x {
    color: #eee;
    &:hover {
      color: #bbb;
    }
  }
}

.author-specs {
  list-style: none;
  padding: 0;
  li {
    margin-left: 30%;
    font-size: .75rem;
  }
  i,
  svg {
    font-size: .75rem;
    line-height: 2rem;
    color: #999;
  }
}

/* ===  FEEDBACK === */

.feedback-avatar {
  border-radius: 50%;
  width: 5vw;
  height: 5vw;
  border: 1px #ccc solid
}

.rating-box {
  i,
  svg {
    //font-size: .75rem;
    color: #ffcc00;
  }
}

.review {
  padding: 1.7777778rem 0;
  background: #fafaf3;
  border-radius: 3px;
  text-align: center;
  color: #333;
  box-shadow: 1px 1px 6px rgba(38, 38, 38, 0.1);
  p {
    font-size: .85rem;
    margin: 0 1rem;
  }
  img {
    position: relative;
    bottom: -1.7777778rem;
    right: 1rem;
    z-index: 0;
  }
}



.Breakdown {
  display: block;
  height: 1.7777778rem;
  margin: 0 1rem;
}

.Breakdown_bg {
  border-radius: 100px;
  background: #efefef;
  min-width: 70%;
  margin: 8px 13px;
  overflow: hidden;
}

.Breakdown_bg,
.Breakdown_count,
.Breakdown_label {
  float: left;
  font-size: 14px;
}

.Breakdown_count {
  text-align: right;
}

.Breakdown_fill {
  background: #3ec9cb;
  height: 16px;
  border-radius: 100px;
}

.Breakdown_label {
  text-align: left;
}

/* ===  OUTLINE === */

.outline table.toc {
  font-size: .85rem;
  td{
    line-height: 1.6rem;
  }
  td:nth-of-type(1){
    padding: 0 1rem 0;
    width: 3rem;
  }
}

/* ===  FOOTER === */

footer.mainfooter {
  background: #3D383E;
  //height: 12rem;
  font-weight: #333;
  padding: 1.7777778rem 0 1.7777778rem 0;
  font-size: .75rem;
  color: #ccc;
  ul li a{
    color: #bbb;
  }
}